<template>
  <div class="body">
    <div class="content">
      <div class="mdui-textfield mdui-textfield-floating-label">
        <label class="mdui-textfield-label">用户名</label>
        <input class="mdui-textfield-input" type="text" v-model="username"/>
      </div>
      <div class="mdui-textfield mdui-textfield-floating-label">
        <label class="mdui-textfield-label">密码</label>
        <input class="mdui-textfield-input" type="password" v-model="password"/>
      </div>
      <div class="mdui-textfield mdui-textfield-floating-label">
        <label class="mdui-textfield-label">确认密码</label>
        <input class="mdui-textfield-input" type="password" v-model="password2"/>
      </div>
      <div class="mdui-textfield mdui-textfield-floating-label">
        <label class="mdui-textfield-label">昵称</label>
        <input class="mdui-textfield-input" type="text" v-model="nickname"/>
      </div>
      <div class="mdui-textfield mdui-textfield-floating-label">
        <div class="mdui-btn mdui-btn-block  mdui-color-blue mdui-ripple mdui-text-color-white" @click="register">注册</div>
      </div>
      <div class="mdui-textfield mdui-textfield-floating-label">
        <div class="mdui-btn mdui-ripple mdui-btn-block mdui-text-color-blue " style="background-color: white;" @click="login">登录</div>
      </div>
    </div>
  </div>
</template>

<script>
  import Tools from '../sdk/Tools'
  export default {
    name: 'views-register',
    data () {
      return {
        username: null,
        password: null,
        password2: null,
        nickname: null
      }
    },
    mounted () {
      this.$_event.addEvent('onOpened', (res) => {
        console.log(res)
      })
    },
    methods: {
      login () {
        this.$router.push('/login')
      },
      register () {
        if (!this.username) {
          this.$_mdui.snackbar({
            message: '请输入用户名',
            position: 'bottom'
          });
          return
        }
        if (!this.password) {
          this.$_mdui.snackbar({
            message: '请输入密码',
            position: 'bottom'
          });
          return
        }
        if (this.password !== this.password2) {
          this.$_mdui.snackbar({
            message: '密码不一致',
            position: 'bottom'
          });
          return
        }
        if (!this.nickname) {
          this.$_mdui.snackbar({
            message: '请输入昵称',
            position: 'bottom'
          });
          return
        }
        let _this = this
        var options = {
          username: this.username,
          password: this.password,
          nickname: this.nickname,
          appKey: WebIM.config.appkey,
          success: function (result) {
            console.log(result)
            _this.$_mdui.snackbar({
              message: '注册成功-请登录',
              position: 'top'
            });
            _this.$router.push('/login')
          },
          error: function (e) {
            if (JSON.parse(e.data).error === 'duplicate_unique_property_exists') {
              _this.$_mdui.snackbar({
                message: '用户名已存在',
                position: 'bottom'
              });
            }
          },
          apiUrl: WebIM.config.apiURL
        };
        this.$_WebIM_conn.registerUser(options);
      }
    }
  }
</script>

<style scoped>
  .body {
    text-align: center;
    padding: 60px 0 50px 0;
  }
  .content {
    display: inline-block;
    width: 230px;
    text-align: left;
  }
</style>



